@import "/variables";

html {
    box-sizing: border-box;
    background: whitesmoke;
}

blockquote {
    border-left: 5px solid $accent;
}
canvas {
	margin: auto;
    display: block;
  
    height: 250px;
    min-height: 150px;
    max-height: 100%;
  
	max-width: 100%;

    background-color: white;
    border: 1px dotted teal;
}
code {
    font-size: 0.9em;
}
pre {
    background-color: rgb(220, 220, 220);
    padding: 0.5em;
    overflow-x: auto;
}

table.highlight>tbody>tr:hover {
    background-color: rgba(255, 235, 59, 0.2);
}

.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
.App-main {
    margin-top: 2em;
    margin-bottom: auto;
}
.App-content {
    max-width: 1000px;
    margin: auto;
    padding: 0 2em;  
}
.App-progress {
    margin: 1em 0 0 0;
}

#grid-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2em;
}
#grid-wrapper > div {
    width: 100%;
}
@media (max-width: 720px) {
    #grid-wrapper {
        grid-template-columns: none;
        grid-template-rows: 1fr 1fr;
        grid-gap: 0;
    }
}

#grid-wrapper-vertical > div {
    width: 100%;
}
#grid-wrapper-vertical {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr;
}
@media (max-width: 720px) {
    #grid-wrapper-vertical {
        display: block;
    }
}

/* Left-right sections - grid elements */
.section-title {
    font-family: monospace;
}
.section-content {
    margin: 0 auto;
}
.section-content > label{
    display: block;
    text-align: center;
}
.section-actions {
    margin: 1em;
    text-align: center;
}
.section-actions button {
    margin: 0 1em;
    text-transform: capitalize;
}

/* Radio input */
.radio-label {
    padding-left: 1em;
    span {
        padding-left: 30px !important;
    }
}
[type="radio"]:checked+span:after,
[type="radio"].with-gap:checked+span:after {
    background-color: $accent !important;
}
[type="radio"]:checked+span:after,
[type="radio"].with-gap:checked+span:before,
[type="radio"].with-gap:checked+span:after {
    background-color: $accent !important;
    border: 2px solid $accent !important;
}

.output-selector {
    text-align: center;
}
@media (min-width: 601px) {
    .how > .row > .col.s12.m6:first-child {
        padding: 0 1.5rem 0 0;
    }
    .how > .row > .col.s12.m6:last-child {
        padding: 0 0 0 1.5rem;
    }
}
.pixel {
    margin-right: 5px;
    width: 2em;
    height: 2em;
    display: inline-block;
    transform: translateY(7px);
}
#p1 {
    background-color: rgba(128, 48, 210, 255);
}
#p2 {
    background-color: rgba(125, 50, 208, 253);
}
.output-selector {
    margin: 0 0 1em 0;
}

.noty_buttons > button {
    margin-left: 1em;
}
